.container{
	background: var(--main-bg);
	position: relative;
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	--color: #92a1a7;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.login-box{
	width: 30%;
	height: 80vh;
	background: var(--sec-bg);
	border-radius: 10px;
	/* border: 1px solid var(--primary); */
	padding: 25px;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: flex-start;
}
.top{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
}

.heading{
	display: flex;
	justify-content: flex-start;
}
.heading > span{
	font-size: 30px;
	font-weight: bold;
	color: var(--primary);
	
}

.bottom{
	display: flex;
	flex-direction: column;
	width: 100%;
	gap: 20px;
}

.fields{
	display: flex;
	flex-direction: column;
	gap: 25px;
}
.field{
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
}
.field > label {
	position: relative;
	color: var(--primary);
	background-color: var(--sec-bg);
	margin-left: 4%;
	margin-bottom: -2.5%;
	z-index: 1;
	padding: 0 10px;
	font-size: 13px;
	font-weight: 600;
}

.field > input, #selectRole {
	position: relative;
	width: 100%;
	height: 45px;
	color: var(--color);
	background: var(--sec-bg);
	outline: none;
	border: 2px solid var(--primary-bg-transp);;
	border-radius: 5px;
	box-sizing: border-box;
	padding: 2px 8px;
	font-size: 15 px;
}

.field > input:focus, #selectRole:focus {
border-color: var(--primary);
}



/* #selectRole:hover {
	background-color: var(--primary);
} */

.showpass{
	position: absolute;
	right: 3%;
	top: 40%;
	cursor: pointer;
}

.options{
	display: flex;
	/* justify-content: space-between; */
	justify-content: flex-end;
	font-size: 12px;
	color: var(--color);
	font-weight: 500;

}
.options .rem{
	position: relative;
	display: flex;
	align-items: center;
	gap: 5%;
	box-sizing: border-box;
	width: 50%;
}

.options .rem > input{
	position: relative;
	cursor: pointer;
	width: 18px;
	height: 18px;
}


.options > .forgot {
	color: var(--primary);
	cursor: pointer;
}

.logIn{
	position: relative;
	background: var(--primary);
	color: var(--main-bg);
	font-size: 18px;
	font-weight: 600;
	padding: 8px;
	border-radius: 5px;
	cursor: pointer;
	/* margin-top: 6%; */
	border: 0;
}
.logIn:disabled{
	background: var(--color);
}

.logIn > img{
	width: 10px;
}

.logIn > .loading{
	position: absolute;
	left: 0;
	top: 0;
	width: 0%;
	height: 100%;
	background: #9fffc89e;
	border-radius: 5px;
	animation: loadingAnima 3000ms cubic-bezier(0.4, 0, 0.2, 1) infinite forwards;
}

@keyframes loadingAnima {
	0%{ width: 0%;}
	10%{ width: 10%;}
	30%{ width: 30%;}
	40%{ width: 40%;}
	50%{ width: 50%;}
	80%{ width: 80%;}
	100%{ width: 100%;}
}